<template>
    <div class="login_container">
        <div style="width:21%;height:25%">
            <div class="login_box">
                <!-- logo -->
                <div class="logo_box">
                    <p class="P_1"></p>
                </div>
                <!--  -->
                <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
                    <!--  -->
                    <el-form-item prop="username">
                        <el-input v-model="loginForm.username" placeholder="" prefix-icon="iconfont icon-user"></el-input>
                    </el-form-item>
                    <!--  -->
                    <el-form-item prop="password">
                        <el-input type="password" v-model="loginForm.password" placeholder="" @keyup.enter.native="login" prefix-icon="iconfont icon-3702mima"></el-input>
                    </el-form-item>
                    <!--  -->
                    <el-form-item class="btns">
                        <el-button type="primary" @click="register"></el-button>
                        <el-button type="primary" @click="login" :disabled="isDisabled"></el-button>
                    </el-form-item>
                </el-form >
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      loginForm: {
        username: '',
        password: ''
      },
      isDisabled:false,
      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: 'JIRA', trigger: 'blur' },
        //   { min: 3, max: 5, message: ' 3  10 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: 'JIRA', trigger: 'blur' },
        //   { min: 3, max: 5, message: ' 3  10 ', trigger: 'blur' }
        ]
      },
      dynamicSegment:"",
      DevicesFileter:'',
    }
  },
  watch:{
      $router(to,from){
          console.log(to)
          console.log(from)
          this.dynamicSegment = to.params.id 
      }
  },
  methods:{
    // 
    timeDelay(){
			const Time_count = 2
			if(!this.timer){
				this.count =Time_count;
				this.isDisabled =true
				this.timer = setInterval(()=>{
					if(this.count>0 && this.count <=Time_count){
						this.count--;
					}else{
						this.isDisabled = false
						clearInterval(this.timer)
						this.timer =null;
					}
				},1000)
			}
		},
    login(){
        this.timeDelay();
        this.$refs.loginFormRef.validate(async valid =>{ //async
            //
            if(!valid) return;
            //
            const {data:res} = await this.$http.post("login",this.loginForm) //
            .catch((error) => {
                console.log(':'+error)
                return this.$message.error(",")//
                }
            );
            //admin 123456
            if(res.meta.status !=200) return this.$message.error("")
            this.$message.success("")
            //1.tokensessionStorage cookie
            this.$cookies.set("token",res.data.dict.token,"7d")
            window.sessionStorage.setItem("token",res.data.dict.token);
            //2.sessionStora
            window.sessionStorage.setItem("email",res.data.dict.email)
            this.$cookies.set("email",res.data.dict.email,"7d")
            window.sessionStorage.setItem("dep",res.data.dict.department)
            window.sessionStorage.setItem("job_number",res.data.dict.job_number)
            window.sessionStorage.setItem("group",res.data.dict.group)
            window.sessionStorage.setItem("phone",res.data.dict.phone)
            window.sessionStorage.setItem("local",res.data.dict.local)
            this.$router.push('./report')
        })
    },
    register(){
        var url = "http://10.0.111.80:8080/login.jsp"
        window.open(url ,'_blank')
    }              
  }
}
</script>

<style >
    .login_container {
        background:url('../assets/image/back.png');
        background-size: 100% 100%;
        height: 100%;
        width: 100%;
        position: fixed;
        margin-top:-10px;
        margin-left: -10px;
    }
    .login_box {
        width: 400px;
        height: 250px;
        background-color:snow ;
        border-radius: 10px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(50%, -50%);
    }
    .logo_box{
        border-radius: 50%;
        margin-left: 18px ;
        margin-top: 0px;
    }
    .P_1 {
        padding-top: 0px;
        margin-top: 15px;
        width: auto;
        height: 50%;
        color: rgb(71, 231, 71);
        font-size:20px ;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
    }
    .btns{
        display: flex;
        justify-content: flex-end;
    }
    .login_form{
        position: absolute;
        bottom: 0;
        width: 100%;
        padding: 0 20px;
        box-sizing: border-box;
    }

</style>
